/* src/styles/App.css */
.container {
  display: flex;
  flex-direction: column;
  align-items: center; /* 가로로 가운데 정렬 */
  justify-content: center; /* 세로로 가운데 정렬 */
  min-height: 100vh;
  padding: 20px;
}

.hero-select-container,
.mod-slot-container,
.mod-list-container {
  width: 100%;
  margin: 0 auto;
}

h1,
h2 {
  font-size: 2rem; /* 제목 글자 크기 확대 */
  text-align: center;
  margin-bottom: 20px;
}

.hero-button,
.mod-slot button {
  font-size: 1.2rem; /* 버튼 크기 확대 */
  padding: 12px 24px;
  width: 100%; /* 버튼의 너비를 100%로 설정하여 더 넓게 */
}

.hero-thumbnail {
  max-width: 100%;
  height: auto;
}
