/* src/styles/ModSlot.css */
.mod-slot-container {
  margin-top: 40px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* 가운데 정렬 */
  flex-wrap: wrap; /* 여러 줄로 감싸도록 설정 */
}

.mod-slot-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 한 줄에 4개의 슬롯 */
  grid-template-rows: repeat(2, 1fr); /* 세로로 두 줄 */
  gap: 15px; /* 슬롯 사이 간격 */
  width: 100%;
  max-width: 960px; /* 전체 너비를 화면에 맞게 제한 */
}

.mod-slot {
  padding: 20px;
  border: 2px solid #b0bec5; /* 테두리 색상 조금 어두운 회색으로 변경 */
  background-color: #e3f2fd; /* 더 밝은 블루 색상 배경 */
  text-align: center;
  font-size: 1.2rem; /* 텍스트 크기 조정 */
  min-height: 120px; /* 슬롯 최소 높이 */
  width: 100%;
  box-sizing: border-box; /* 패딩 포함 */
  border-radius: 8px; /* 슬롯의 모서리 둥글게 처리 */
  transition: all 0.3s ease; /* 부드러운 전환 효과 */
}

.mod-slot:hover {
  background-color: #bbdefb; /* 호버 시 배경색 살짝 진하게 */
  border-color: #90a4ae; /* 테두리 색도 살짝 진하게 */
}

button {
  background-color: #007bff; /* 파란색 버튼 */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1.1rem;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 10px;
  width: 100%;
  transition: background-color 0.3s ease; /* 버튼 색상 전환 부드럽게 */
}

button:hover {
  background-color: #0056b3; /* 호버 시 버튼 색상 */
}

button:disabled {
  background-color: #b0bec5; /* 비활성화된 버튼 색상 */
  cursor: not-allowed;
}
