/* src/styles/ModList.css */
.mod-list-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 200px; /* 리스트 아래에 여백 추가 */
}

.mod-list-container h2 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333; /* 제목 색상 */
}

.mod-list-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 가로 4개 */
  grid-template-rows: repeat(5, 1fr); /* 세로 5개 */
  gap: 10px;
  width: 100%;
  height: 500px; /* 세로 길이를 제한해서 스크롤이 가능하게 함 */
  overflow-y: auto; /* 세로 스크롤 활성화 */
}

.mod-list-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  border: 2px solid #4caf50; /* 테두리 색상 */
  border-radius: 8px;
  background-color: #e8f5e9; /* 아이템 배경 색상 */
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease,
    box-shadow 0.3s ease;
}

.mod-list-item:hover {
  background-color: #c8e6c9; /* 호버 시 배경 색상 */
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 128, 0, 0.3); /* 그림자 효과 */
}

.mod-list-item:active {
  background-color: #a5d6a7; /* 클릭 시 배경 색상 */
  transform: scale(0.95);
}

.mod-list-item:disabled {
  background-color: #e0e0e0; /* 비활성화된 아이템 색상 */
  cursor: not-allowed;
}

.mod-name {
  font-weight: bold;
  color: #388e3c; /* 모드 이름 색상 */
}

.mod-description {
  font-size: 12px;
  color: #555; /* 설명 텍스트 색상 */
}

.margin {
  min-height: 60px;
}
