.hero-select-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.hero-role-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
  gap: 20px;
}

.hero-role-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  min-height: 300px;
}

.tank {
  align-self: flex-start;
}

.damage {
  align-self: center;
  width: 40%;
}

.support {
  align-self: flex-end;
}

.hero-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 5px;
  justify-content: center;
  width: 100%;
}

.tank-grid {
  grid-template-columns: repeat(4, 1fr);
}

.damage-grid {
  grid-template-columns: repeat(6, 1fr);
}

.support-grid {
  grid-template-columns: repeat(4, 1fr);
}

.hero-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: none;
  background: none;
  cursor: pointer;
}

.hero-thumbnail {
  width: 100px;
  height: auto;
}
